var render = function render(){var _vm=this,_c=_vm._self._c,_setup=_vm._self._setupProxy;return _c('v-container',[_c('h1',{staticClass:"font-weight-light"},[_vm._v(" Publications ")]),_c('h3',{staticClass:"font-weight-light"},[_vm._v(" This page lists all Unipept peer-reviewed journal articles in reverse-chronological order. ")]),_c(_setup.PublicationCard,{staticClass:"mt-5",attrs:{"title":"Unipept Desktop: a faster, more powerful metaproteomics results analysis tool","authors":["Pieter Verschaffelt", "Tim Van Den Bossche", "Lennart Martens", "Peter Dawyndt", "Bart Mesuere"],"journal":"Journal of Proteome Research","year":"2021","doi":"doi.org/10.1021/acs.jproteome.0c00855","googleScholar":"https://scholar.google.com/scholar?cluster=14287692748263471832&hl=nl&as_sdt=0,5","image":require('@/assets/publications-2021.jpeg')},scopedSlots:_vm._u([{key:"abstract",fn:function(){return [_vm._v(" Metaproteomics has become an important research tool to study microbial systems, which has resulted in increased metaproteomics data generation. However, efficient tools for processing the acquired data have lagged behind. One widely used tool for metaproteomics data interpretation is Unipept, a web-based tool that provides, among others, interactive and insightful visualizations. Due to its web-based implementation, however, the Unipept web application is limited in the amount of data that can be analyzed. In this manuscript we therefore present Unipept Desktop, a desktop application version of Unipept that is designed to drastically increase the throughput and capacity of metaproteomics data analysis. Moreover, it provides a novel comparative analysis pipeline and improves the organization of experimental data into projects, thus addressing the growing need for more efficient and versatile analysis tools for metaproteomics data. ")]},proxy:true}])}),_c(_setup.PublicationCard,{staticClass:"mt-5",attrs:{"title":"Unipept CLI 2.0: adding support for visualizations and functional annotations","authors":["Pieter Verschaffelt", "Philippe Van Thienen", "Tim Van Den Bossche", "Felix Van der Jeugt", "Caroline De Tender", "Lennart Martens", "Peter Dawyndt", "Bart Mesuere"],"journal":"Bioinformatics","year":"2020","doi":"doi.org/10.1093/bioinformatics/btaa553","googleScholar":"https://scholar.google.com/scholar?cluster=12996336077971755665&hl=nl&as_sdt=0,5","image":require('@/assets/publications-2020.jpeg')},scopedSlots:_vm._u([{key:"abstract",fn:function(){return [_vm._v(" Unipept is an ecosystem of tools developed for fast metaproteomics data-analysis consisting of a web application, a set of web services (application programming interface, "),_c(_setup.Initialism,[_vm._v("API")]),_vm._v(") and a command-line interface ("),_c(_setup.Initialism,[_vm._v("CLI")]),_vm._v("). After the successful introduction of version 4 of the Unipept web application, we here introduce version 2.0 of the "),_c(_setup.Initialism,[_vm._v("API")]),_vm._v(" and "),_c(_setup.Initialism,[_vm._v("CLI")]),_vm._v(". Next to the existing taxonomic analysis, version 2.0 of the "),_c(_setup.Initialism,[_vm._v("API")]),_vm._v(" and "),_c(_setup.Initialism,[_vm._v("CLI")]),_vm._v(" provides access to Unipepts powerful functional analysis for metaproteomics samples. The functional analysis pipeline supports retrieval of Enzyme Commission numbers, Gene Ontology terms and InterPro entries for the individual peptides in a metaproteomics sample. This paves the way for other applications and developers to integrate these new information sources into their data processing pipelines, which greatly increases insight into the functions performed by the organisms in a specific environment. Both the "),_c(_setup.Initialism,[_vm._v("API")]),_vm._v(" and "),_c(_setup.Initialism,[_vm._v("CLI")]),_vm._v(" have also been expanded with the ability to render interactive visualizations from a list of taxon ids. These visualizations are automatically made available on a dedicated website and can easily be shared by users. ")]},proxy:true}])}),_c(_setup.PublicationCard,{staticClass:"mt-5",attrs:{"title":"Unipept 4.0: functional analysis of metaproteome data","authors":["Robbert Gurdeep Singh", "Alessandro Tanca", "Antonio Palomba", "Felix Van der Jeugt", "Pieter Verschaffelt", "Sergio Uzzau", "Lennart Martens", "Peter Dawyndt", "Bart Mesuere"],"journal":"Journal of Proteome Research","year":"2019","doi":"doi.org/10.1021/acs.jproteome.8b00716","googleScholar":"https://scholar.google.be/scholar?cluster=10704911090193210592&hl=en&as_sdt=0,5","image":require('@/assets/publications-2019.jpeg')},scopedSlots:_vm._u([{key:"abstract",fn:function(){return [_vm._v(" Unipept is a web application for metaproteome data analysis, with an initial focus on tryptic-peptide-based biodiversity analysis of "),_c(_setup.Initialism,[_vm._v("MS/MS")]),_vm._v(" samples. Because the true potential of metaproteomics lies in gaining insight into the expressed functions of complex environmental samples, the 4.0 release of Unipept introduces complementary functional analysis based on "),_c(_setup.Initialism,[_vm._v("GO")]),_vm._v(" terms and "),_c(_setup.Initialism,[_vm._v("EC")]),_vm._v(" numbers. Integration of this new functional analysis with the existing biodiversity analysis is an important asset of the extended pipeline. As a proof of concept, a human faecal metaproteome data set from 15 healthy subjects was reanalyzed with Unipept 4.0, yielding fast, detailed, and straightforward characterization of taxon-specific catalytic functions that is shown to be consistent with previous results from a "),_c(_setup.Initialism,[_vm._v("BLAST")]),_vm._v("-based functional analysis of the same data. ")]},proxy:true}])}),_c(_setup.PublicationCard,{staticClass:"mt-5",attrs:{"title":"High-throughput metaproteomics data analysis with Unipept: a tutorial","authors":["Bart Mesuere", "Felix Van der Jeugt", "Toon Willems", "Tom Naessens", "Bart Devreese", "Lennart Martens", "Peter Dawyndt"],"journal":"Journal of Proteomics","year":"2018","extra":"171, pp 11-22","doi":"doi.org/10.1016/j.jprot.2017.05.022","googleScholar":"https://scholar.google.be/scholar?cluster=6566003157091239958&hl=en&as_sdt=0,5","image":require('@/assets/publications-2018.jpeg')},scopedSlots:_vm._u([{key:"abstract",fn:function(){return [_vm._v(" In recent years, shotgun metaproteomics has established itself as an important tool to study the composition of complex ecosystems and microbial communities. Two key steps in metaproteomics data analysis are the inference of proteins from the identified peptides, and the determination of the taxonomic origin and function of these proteins. This tutorial therefore introduces the Unipept command line interface ("),_c(_setup.RLink,{attrs:{"to":"/clidocs","router":""}},[_vm._v("http://unipept.ugent.be/clidocs")]),_vm._v(" ) as a platform-independent tool for such metaproteomics data analyses. First, a detailed overview is given of the available Unipept commands and their functions. Next, the power of the Unipept command line interface is illustrated using two case studies that analyze a single tryptic peptide, and a set of peptides retrieved from a shotgun metaproteomics experiment, respectively. Finally, the analysis results obtained using these command line tools are compared with the interactive taxonomic analysis that is available on the Unipept website. ")]},proxy:true}])}),_c(_setup.PublicationCard,{staticClass:"mt-5",attrs:{"title":"The unique peptidome: Taxon-specific tryptic peptides as biomarkers for targeted metaproteomics","authors":["Bart Mesuere", "Felix Van der Jeugt", "Bart Devreese", "Peter Vandamme", "Peter Dawyndt"],"journal":"Proteomics","year":"2016","extra":"16 (17), pp 23132318","doi":"doi.org/10.1002/pmic.201600023","googleScholar":"https://scholar.google.com/scholar?cluster=10033728957067620724&hl=en&as_sdt=0,5","image":require('@/assets/publications-2016b.jpeg')},scopedSlots:_vm._u([{key:"abstract",fn:function(){return [_vm._v(" The Unique Peptide Finder is an interactive web application to quickly hunt for tryptic peptides that are unique to a particular species, genus, or any other taxon. Biodiversity within the target taxon is represented by a set of proteomes selected from a monthly updated list of complete and nonredundant UniProt proteomes, supplemented with proprietary proteomes loaded into persistent local browser storage. The software computes and visualizes pan and core peptidomes as unions and intersections of tryptic peptides occurring in the selected proteomes. In addition, it also computes and displays unique peptidomes as the set of all tryptic peptides that occur in all selected proteomes but not in any UniProt record not assigned to the target taxon. As a result, the unique peptides can serve as robust biomarkers for the target taxon, for example, in targeted metaproteomics studies. Computations are extremely fast since they are underpinned by the Unipept database, the lowest common ancestor algorithm implemented in Unipept and modern web technologies that facilitate in-browser data storage and parallel processing. ")]},proxy:true}])}),_c(_setup.PublicationCard,{staticClass:"mt-5",attrs:{"title":"Unipept Web Services for Metaproteomics Analysis","authors":["Bart Mesuere", "Toon Willems", "Felix Van der Jeugt", "Bart Devreese", "Peter Vandamme", "Peter Dawyndt"],"journal":"Bioinformatics","year":"2016","extra":"32 (11), pp 1746-1748","doi":"doi.org/10.1093/bioinformatics/btw039","googleScholar":"https://scholar.google.be/scholar?cluster=6023517943371055318&hl=en&as_sdt=0,5","image":require('@/assets/publications-2016.jpg')},scopedSlots:_vm._u([{key:"abstract",fn:function(){return [_vm._v(" Unipept is an open source web application that is designed for metaproteomics analysis with a focus on interactive datavisualization. It is underpinned by a fast index built from UniProtKB and the "),_c(_setup.Initialism,[_vm._v("NCBI")]),_vm._v(" taxonomy that enables quick retrieval of all UniProt entries in which a given tryptic peptide occurs. Unipept version 2.4 introduced web services that provide programmatic access to the metaproteomics analysis features. This enables integration of Unipept functionality in custom applications and data processing pipelines. Availability and Implementation The web services are freely available at "),_c(_setup.RLink,{attrs:{"to":"http://api.unipept.ugent.be"}},[_vm._v("http://api.unipept.ugent.be")]),_vm._v("> and are open sourced under the "),_c(_setup.Initialism,[_vm._v("MIT")]),_vm._v(" license. ")]},proxy:true}])}),_c(_setup.PublicationCard,{staticClass:"mt-5",attrs:{"title":"The Unipept Metaproteomics Analysis Pipeline","authors":["Bart Mesuere", "Griet Debyser", "Maarten Aerts", "Bart Devreese", "Peter Vandamme", "Peter Dawyndt"],"journal":"Proteomics","year":"2015","extra":"15 (8), pp 14371442","doi":"doi.org/10.1002/pmic.201400361","googleScholar":"https://scholar.google.be/scholar?cluster=4081421507601531351&hl=en&as_sdt=0,5","image":require('@/assets/publications-2015.jpg')},scopedSlots:_vm._u([{key:"abstract",fn:function(){return [_vm._v(" Unipept ("),_c(_setup.RLink,{attrs:{"to":"/","router":""}},[_vm._v("http://unipept.ugent.be")]),_vm._v(") is a web application that offers a user-friendly way to explore the biodiversity of complex metaproteome samples by providing interactive visualizations. In this article, the updates and changes to Unipept since its initial release are presented. This includes the addition of interactive sunburst and treeview visualizations to the multipeptide analysis, the foundations of an application programming interface ("),_c(_setup.Initialism,[_vm._v("API")]),_vm._v(") and a command line interface, updated data sources, and the open-sourcing of the entire application under the "),_c(_setup.Initialism,[_vm._v("MIT")]),_vm._v(" license. ")]},proxy:true}])}),_c(_setup.PublicationCard,{staticClass:"mt-5",attrs:{"title":"Unipept: Tryptic Peptide-Based Biodiversity Analysis of Metaproteome Samples","authors":["Bart Mesuere", "Bart Devreese", "Griet Debyser", "Maarten Aerts", "Peter Vandamme", "Peter Dawyndt"],"journal":"J. Proteome Res.","year":"2012","extra":"11 (12), pp 57735780","doi":"doi.org/10.1021/pr300576s","googleScholar":"https://scholar.google.be/scholar?cluster=13083723326591523916&hl=en&as_sdt=2005&sciodt=0,5","image":require('@/assets/publications-2012.jpg')},scopedSlots:_vm._u([{key:"abstract",fn:function(){return [_vm._v(" The Unipept web application ("),_c(_setup.RLink,{attrs:{"to":"/","router":""}},[_vm._v("http://unipept.ugent.be")]),_vm._v(") supports biodiversity analysis of large and complex metaproteome samples using tryptic peptide information obtained from shotgun "),_c(_setup.Initialism,[_vm._v("MS/MS")]),_vm._v(" experiments. Its underlying index structure is designed to quickly retrieve all occurrences of a tryptic peptide in UniProt entries. Taxon-specificity of the tryptic peptide is successively derived from these occurrences using a novel lowest common ancestor approach that is robust against taxonomic misarrangements, misidentifications, and inaccuracies. Not taking into account this identification noise would otherwise result in drastic loss of information. Dynamic treemaps visualize the biodiversity of metaproteome samples, which eases the exploration of samples with highly complex compositions. The potential of Unipept to gain novel insights into the biodiversity of a sample is evaluated by reanalyzing publicly available metaproteome data sets taken from the bacterial phyllosphere and the human gut. ")]},proxy:true}])})],1)
